.boilerplate-frontend {
  border-radius: 5px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.07);
  background-color: #b8c9f8;
  border: 2px solid #b8c9f8;
  color: #000000;
  padding: 20px;
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    margin: 0;
    padding: 0;
  }
}

@media (min-width: 35em) {
  .stemfb-content-box {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
}

@media (max-width: 35em) {
  .stemfb-content-box {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}
